import Vue from 'vue'
import Vuex from 'vuex'
import Web3 from 'web3'
import * as types from './mutation-types'
import * as api from '../api'
import router from 'vue-router'

Vue.use(Vuex);
Vue.use(router);
const state = {
  loading:false,
  account: '',
  balance: '',
  user:{
    investGroup:[0,0,0],
    jackpot:0
  },
  abi:JSON.parse('[{"constant":true,"inputs":[],"name":"name","outputs":[{"name":"","type":"string"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"name":"_spender","type":"address"},{"name":"_value","type":"uint256"}],"name":"approve","outputs":[{"name":"success","type":"bool"}],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[],"name":"getBalance","outputs":[{"name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"getSmallJackpot","outputs":[{"name":"smallJackpots","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"totalSupply","outputs":[{"name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[],"name":"tickets","outputs":[],"payable":true,"stateMutability":"payable","type":"function"},{"constant":false,"inputs":[{"name":"_from","type":"address"},{"name":"_to","type":"address"},{"name":"_value","type":"uint256"}],"name":"transferFrom","outputs":[{"name":"success","type":"bool"}],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":false,"inputs":[{"name":"_users","type":"address[]"}],"name":"bigCheckOut","outputs":[],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[],"name":"getTotalConsume","outputs":[{"name":"totalConsumes","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"getRate","outputs":[{"name":"rates","type":"uint32"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[{"name":"_owner","type":"address"}],"name":"balanceOf","outputs":[{"name":"balance","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[],"name":"ticketConsume","outputs":[],"payable":true,"stateMutability":"payable","type":"function"},{"constant":false,"inputs":[{"name":"_users","type":"address[]"}],"name":"smallCheckOut","outputs":[],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[],"name":"getConsume","outputs":[{"name":"consumes","type":"uint32"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"name":"_price","type":"uint256"}],"name":"getEth","outputs":[],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[],"name":"symbol","outputs":[{"name":"","type":"string"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"getBigJackpot","outputs":[{"name":"bigJackpots","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"name":"_to","type":"address"},{"name":"_value","type":"uint256"}],"name":"transfer","outputs":[{"name":"success","type":"bool"}],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[{"name":"_owner","type":"address"}],"name":"trashOf","outputs":[{"name":"trashs","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"name":"_users","type":"address[]"},{"name":"_prices","type":"uint256[]"},{"name":"_allPrices","type":"uint256"}],"name":"sendAll","outputs":[],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[{"name":"_owner","type":"address"},{"name":"_spender","type":"address"}],"name":"allowance","outputs":[{"name":"remaining","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"name":"_rate","type":"uint32"}],"name":"setRate","outputs":[],"payable":false,"stateMutability":"nonpayable","type":"function"},{"inputs":[{"name":"_initialAmount","type":"uint256"},{"name":"_tokenName","type":"string"},{"name":"_rate","type":"uint32"}],"payable":true,"stateMutability":"payable","type":"constructor"},{"payable":true,"stateMutability":"payable","type":"fallback"},{"anonymous":false,"inputs":[{"indexed":true,"name":"_from","type":"address"},{"indexed":true,"name":"_to","type":"address"},{"indexed":false,"name":"_value","type":"uint256"}],"name":"Transfer","type":"event"},{"anonymous":false,"inputs":[{"indexed":true,"name":"_from","type":"address"},{"indexed":false,"name":"_value","type":"uint256"}],"name":"InTrash","type":"event"},{"anonymous":false,"inputs":[{"indexed":true,"name":"_owner","type":"address"},{"indexed":true,"name":"_spender","type":"address"},{"indexed":false,"name":"_value","type":"uint256"}],"name":"Approval","type":"event"},{"anonymous":false,"inputs":[{"indexed":false,"name":"","type":"address"},{"indexed":false,"name":"","type":"string"},{"indexed":false,"name":"","type":"uint256"}],"name":"transferLogs","type":"event"}]'),
  at:"0xA3a5D48921cDC1dEb48cb0528DDfAB94d4AC6a6e",
  sys:{},
  jp:{},
  bp:{},
  contractInfo:{
    bigJackpot:0, // 奖池
    smallJackpot:0, // 奖池
    consume:0,
    totalConsume:0,
    rate:0,
    balanceOf:0 // 当前用户代币量
  }
};
const getters = {
  loading: state => state.loading,
  account: state => state.account,
  balance: state => state.balance,
  user: state => state.user?state.user:null,
  sys: state => state.sys,
  jp: state => state.jp,
  bp: state => state.bp,
  plan: state => state.plan,
  contractInfo: state=>state.contractInfo
};

const actions = {
  // 初始化Web3
  async initWeb3({dispatch}) {
    if (typeof web3 !== 'undefined') {
      window.web3 = new Web3(Web3.givenProvider || "ws://localhost:8546");
      window.BN = web3.utils.BN;
      dispatch('contract');
      let net = await web3.eth.net.getId();
      if(net===1) {
        dispatch('watchWeb3Account');
      }else{
        alert('您当前的网络不是主链')
      }
    }
  },
  initService({commit}){
    api.getSys().then(result=>{
      if(!result) return;
      commit(types.UPDATE_SYSTEM, result.data.sys);
      commit(types.UPDATE_JACKPOT, result.data.jp);
      commit(types.UPDATE_BJACKPOT, result.data.bp);
    });
    api.getPlan().then(result=>{
      if(!result) return;
      commit(types.UPDATE_PLAN, result.data);
    });
  },
  newContract({commit,state}){
    contract.methods.getRate().call({},(e,r)=>{commit(types.UPDATE_RATE,parseInt(r))});
    contract.methods.getConsume().call({},(e,r)=>{commit(types.UPDATE_CONSUME,parseInt(r))});
    contract.methods.getSmallJackpot().call({},(e,r)=>{commit(types.UPDATE_SMALLJACKPOT,parseFloat(web3.utils.fromWei(r,'ether')))});
    contract.methods.getBigJackpot().call({},(e,r)=>{commit(types.UPDATE_BIGJACKPOT,parseFloat(web3.utils.fromWei(r,'ether')))});
    contract.methods.getTotalConsume().call({},(e,r)=>{commit(types.UPDATE_TOTALCONSUME,parseInt(r))});
    contract.methods.balanceOf(state.account).call({},(e,r)=>{commit(types.UPDATE_BALANCEOF,parseInt(r))});
  },
  // 加入合约接口
  contract({state}) {
    window.contract = new web3.eth.Contract(state.abi,state.at);
  },
  // 监听并获取钱包地址
  watchWeb3Account({commit, dispatch, state}) {
    setInterval(async () => {
      const accounts = await web3.eth.getAccounts();
      if (!accounts) return;
      const activeAccount = accounts[0];
      // const activeAccount = "0x27068099754ca912fb44b246da432667ae980e49";
      if (!activeAccount) return;
      if (activeAccount !== state.account) {
        commit(types.UPDATE_ACCOUNT, activeAccount);
        // 以钱包地址获取用户信息
        api.login({addr:activeAccount,sup:getQueryVariable('sup')}).then(result => {
          if(!result) return ;
          if(result.code&&result.code===201) vue.$Message.success(vue.$t('tips.reg'));
          result = result.data;
          commit(types.UPDATE_USER, result.user);
          dispatch('checkHash');
          sessionStorage.setItem('token',result.token);
        });
        dispatch('getAccountBalance');
        dispatch('newContract');
      }
    }, 1000)
  },
  // 检查哈希
  checkHash({state,commit}){
    let user = state.user;
    let hash = user.hash;
    for(const item of hash){
      if(item.status) continue;
      vue.$store.commit(types.LOADDING,true);
      let getHashVal=setInterval(async e=>{
        let a = await web3.eth.getTransactionReceipt(item.hash);
        if(a){
          await api.confirmHash({item,status:a.status});
          vue.$store.commit(types.LOADDING,false);
          clearInterval(getHashVal);
        }
      },5000)
    }
  },
  // 获取钱包余额
  getAccountBalance({commit, state}) {
    web3.eth.getBalance(state.account).then(b=>{
      commit(types.UPDATE_GETACCOUNTBALANCE, parseFloat(web3.utils.fromWei(b,'ether')))
    });
  }
};

const mutations = {
  [types.LOADDING](state, val) {
    state.loading = val;
  },
  [types.UPDATE_ACCOUNT](state, account) {
    state.account = account;
  },
  [types.UPDATE_USER](state, user) {
    state.user = user;
  },
  [types.UPDATE_SYSTEM](state, sys) {
    state.sys = sys;
  },
  [types.UPDATE_BIGJACKPOT](state,val){
    state.contractInfo.bigJackpot = val;
  },
  [types.UPDATE_SMALLJACKPOT](state,val){
    state.contractInfo.smallJackpot = val;
  },
  [types.UPDATE_TOTALCONSUME](state,val){
    state.contractInfo.totalConsume = val;
  },
  [types.UPDATE_CONSUME](state,val){
    state.contractInfo.consume = val;
  },
  [types.UPDATE_RATE](state,val){
    state.contractInfo.rate = val;
  },
  [types.UPDATE_PLAN](state, plan) {
    state.plan = plan;
  },
  [types.UPDATE_BALANCEOF](state, val) {
    state.contractInfo.balanceOf = val;
  },
  [types.UPDATE_JACKPOT](state, jp) {
    state.jp = jp;
  },
  [types.UPDATE_BJACKPOT](state, bp) {
    state.bp = bp;
  },
  [types.UPDATE_GETACCOUNTBALANCE](state, balance) {
    state.balance = balance;
  },
};
//
export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})

function getQueryVariable(variable) {
  let query = window.location.search.substring(1);
  let vars = query.split("&");
  for (let i = 0; i < vars.length; i++) {
    let pair = vars[i].split("=");
    if (pair[0] == variable) return pair[1];
  }
  return (false);
}



